.socials {
  max-width: 100px;

  li {
    min-width: 25px;
    min-height: 25px;
    margin-right: 11px;
    margin-bottom: 8px;

    a {
      position: relative;
      min-width: 25px;
      min-height: 25px;

      line-height: 0;

      background-color: $color-default-white;
      border-radius: 50%;

      @include hover-focus {
        background-color: $color-egyptian-blue;
        outline: none;

        svg {
          color: $color-default-white;
        }
      }

      @include active {
        background-color: $color-sapphire;

        svg {
          color: $color-default-white;
        }
      }

      &.disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }

    svg {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
    }
  }

  li:nth-of-type(3n),
  li:last-of-type {
    margin-right: 0;
  }
}
